          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <ul id="menu" class="nav">
        <li v-for="(model,index) in models" :key="model.index" :data-menuanchor="'page'+index" class="nav-item">
          <a :href="'#page'+index">{{model.name}}</a>
        </li>
      </ul>
    </div>
    <el-row :span="24" class="actions les">
      <el-button
        v-for="(le,leindex) in les"
        :key="le.GUID"
        @click="playle(le,leindex)"
        type="primary"
        plain
        icon="el-icon-search"
        round
      >{{le.Name}}</el-button>
    </el-row>
    <el-row :span="24" class="actions oper">
      <el-button-group>
        <el-button icon="el-icon-zoom-in" @click="zoomChange(1)"></el-button>
        <el-button icon="el-icon-zoom-out" @click="zoomChange(-1)"></el-button>
      </el-button-group>
      <i class="el-icon-minus" style="color:#00c2ff;margin:auto;"></i>
      <el-radio-group v-model="playspeed" @change="playSpeedChange">
        <el-radio-button :label="30">快速</el-radio-button>
        <el-radio-button :label="100">正常</el-radio-button>
        <el-radio-button :label="150">慢速</el-radio-button>
      </el-radio-group>
      <i class="el-icon-minus" style="color:#00c2ff;margin:auto;"></i>
      <el-button @click="stople(devnowindex)" type="danger" plain icon="el-icon-circle-close" round>停止</el-button>
    </el-row>
    <div id="footer">Copyright © 2019 solopaul All rights reserved</div>
    <full-page ref="fullpage" :options="fpopt" id="fullpage">
      <div class="section" v-for="(dev,index) in devices" :key="dev.index">
        <Device
          :ref="'device'+index"
          :deviceid="dev.deviceid"
          :keys="dev.keys"
          :orgwidth="dev.orgwidth"
          :orgheight="dev.orgheight"
          :zoomin="dev.zoomin"
          :keycap="dev.img_keycap"
          :panel="dev.img_panel"
          :outline="dev.img_outline"
          :showwidth="device.showwidth"
          :showheight="device.showheight"
          :lespeed="playspeed"
        />
      </div>
    </full-page>
  </div>
</template>

<script>
import Device from "./components/Device.vue";
import { modellist } from "./device/modellist.js";
import { lesall } from "./device/les.js";
import { device1 } from "./device/device1.js";
import { device2 } from "./device/device2.js";
import { device3 } from "./device/device3.js";
import { device4 } from "./device/device4.js";
var devices = [];
devices.push(device1);
devices.push(device2);
devices.push(device3);
devices.push(device4);
var anchors = [];
for(let i=0;i<devices.length;i++){
  anchors.push("page"+i);
}
let boxwidth = window.innerWidth;
let boxheight = window.innerHeight;
export default {
  name: "app",
  data: function() {
    return {
      fpopt: {
        //licenseKey: "YOUR_KEY_HEERE",
        menu: "#menu",
        scrollOverflow: true,
        scrollBar: false,
        lockAnchors: false,
        navigation: true,
        dragAndMove: true,
        anchors: anchors,
        sectionsColor: [
          "#41b883",
          "#ff5f45",
          "#0798ec",
          "#fec401",
          "#1bcee6",
          "#ee1a59",
          "#2c3e4f",
          "#ba5be9",
          "#b4b8ab"
        ],
        afterLoad: (origin, destination, direction)=>{
          //using index
          this.devorgindex = origin.index;
          this.devnowindex = destination.index;
          this.les = lesall[destination.index];
          this.leplayindex = 0;
          this.stople(origin.index);
          this.playle(this.les[0],0);
          //using anchorLink
          // if(origin.anchor == 'page3'){
          //   alert("Section 2 ended loading");
          // }
        }
      },
      models: modellist,
      devices: devices,
      device: {
        showwidth: boxwidth,
        showheight: boxheight
      },
      les: [],
      devorgindex: 0,
      devnowindex: 0,
      playspeed: 100,
      leplayindex: 0,
      activeIndex: "2"
    };
  },
  components: {
    Device
  },
  mounted: function() {
    //console.log(222,this.$refs['device'+1],11111111)
    window.onresize = () => {
      this.device.showwidth = window.innerWidth;
      this.device.showheight = window.innerHeight;
    };
    this.$nextTick(function(){
      //console.log(this.$refs.device1[0].deviceid);
      //this.$refs.device1[0].playle(this.lesall[0][0]);
    });
  },
  methods: {
    handleSelect: function(val) {
      console.log(val);
    },
    playle(le,leindex) {
      this.leplayindex = leindex;
      this.$refs['device'+this.devnowindex][0].playLe(le);
    },
    stople(devindex) {
      if(!devindex){
        devindex = 0;
      }
      this.$refs['device'+devindex][0].stopLe();
    },
    playSpeedChange(val){
      this.$refs['device'+this.devnowindex][0].playLe(this.les[this.leplayindex]);
    },
    zoomChange(type){
      if(type==1){
        this.devices[this.devnowindex].zoomin += 0.1;
      }else{
        this.devices[this.devnowindex].zoomin -= 0.1;
      }
    }
  }
};
</script>

